*,
*::before,
*::after {
  box-sizing: inherit;
  outline: 0.1rem dashed lightgrey; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  color: #ffffff;
  background-color: #2c2929; }

body {
  overflow-x: hidden;
  max-width: 100%;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  font-size: 1.4rem;
  line-height: 1.2;
  -webkit-font-smoothing: antialiased;
  font-variant-numeric: proportional-nums;
  font-feature-settings: "pnum"; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 0.1rem;
  height: 0.1rem;
  margin: -0.1rem;
  padding: 0;
  border: 0; }

img {
  width: 100%;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  color: #ed4040; }
  a:hover, a:active {
    text-decoration: underline; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0; }
  .navigation__menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1;
    width: 100%;
    margin: 0;
    padding: 0; }
  .navigation__menu-item {
    text-transform: uppercase; }
    .navigation__menu-item:first-child {
      margin-left: 0; }
    .navigation__menu-item:last-child {
      margin-right: 0; }
  .navigation__menu-link {
    text-transform: uppercase; }
  @media (min-width: 768px) {
    .navigation {
      display: flex;
      flex-direction: row;
      justify-content: center;
      padding: 0; }
      .navigation__menu {
        display: flex;
        flex-direction: row;
        justify-content: center;
        justify-content: flex-end;
        padding: 0; }
      .navigation__menu-item {
        margin: 0 1.5rem; } }



